.video-detail {
  min-height: 95vh;
}

.video-detail__container {
  flex-direction: column !important;
}

.video-detail__player {
  flex: 1;
}

.video-detail__player_wrapper {
  position: sticky;
  top: 86px;
  width: 100%;
  color: #ffffff;
}

.video-detail__player_react-player {
  height: 77vh !important;
  width: 100% !important;
}

.video-detail__player_title {
  padding: 16px;
  font-weight: bold !important;
  font-size: 1.5rem !important;
  line-height: 1.334 !important;
  letter-spacing: 0em !important;
}

.video-detail__player_description {
  padding: 8px 16px;
  flex-direction: row !important;
  justify-content: space-between;
}

.video-detail__player_description_title {
  line-height: 1.75 !important;
  color: #ffffff;
}

.video-detail__player_description_icon {
  margin-left: 5px;
  font-size: 12px !important;
  color: #808080;
}

.video-detail__player_description_container {
  flex-direction: row !important;
  align-items: center;
  column-gap: 20px;
}

.video-detail__player_description_container p {
  opacity: 0.7;
}

.video-detail__videos {
  padding: 40px 16px;
}

@media (max-width: 600px) {
  .video-detail__player_react-player {
    height: 45vh !important;
  }
}

@media (min-width: 900px) {
  .video-detail__container {
    flex-direction: row !important;
  }

  .video-detail__videos {
    padding: 8px 16px;
  }
}
