.sidebar {
  height: auto;
  flex-direction: row !important;
  overflow-y: auto;
}

.sidebar__btn {
  padding: 7px 15px;
  margin: 10px 0px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: transparent;
  border: none;
  border-radius: 20px;
  font-weight: bold !important;
  text-transform: capitalize;
  color: #ffffff;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
}

.sidebar__btn:hover {
  background-color: #f31503 !important;
  color: #ffffff !important;
}

.sidebar__btn:hover span {
  color: #ffffff !important;

}

.sidebar__btn--selected {
  background: #f31503;
}

.sidebar__icon {
  margin-right: 15px;
  color: #ff0000;
}

.sidebar__icon--selected {
  background: #ffffff;
}

.sidebar__name {
  opacity: 0.8;
}

.sidebar__name--selected {
  opacity: 1;
}

@media (min-width: 900px) {
  .sidebar {
    height: 95%;
    flex-direction: column !important;
  }

  .sidebar__btn {
    margin: 10px;
  }
}
