.feed {
  flex-direction: column !important;
}

.feed__sidebar-box {
  height: auto;
  border-right: 1px solid #3d3d3d;
}

.feed__copyright {
  margin-top: 12px !important;
  color: #ffffff;
}

.feed__videos-box {
  height: 90vh;
  padding: 16px;
  flex-grow: 2;
  flex-shrink: 1;
  flex-basis: 0%;
  overflow-y: auto;
}

.feed__videos-typography {
  margin-bottom: 16px !important;
  font-weight: bold !important;
  color: #ffffff;
}

.feed__videos-typography span {
  color: #f31503;
}

@media (min-width: 900px) {
  .feed {
    flex-direction: row !important;
  }

  .feed__sidebar-box {
    height: 92vh;
    padding: 0 16px;
  }

  .feed__copyright {
    display: none !important;
  }
}
