.video-card {
  box-shadow: none !important;
  border-radius: 0 !important;
}

.video-card__poster {
  height: 180px;
}

.video-card__content {
  background-color: #1e1e1e;
  height: 106px;
}

.video-card__title {
  font-weight: bold !important;
  color: #ffffff;
}

.video-card__subtitle {
  font-weight: bold !important;
  color: #808080;
}

.video-card__icon {
  font-size: 12px !important;
  color: #808080;
  margin-left: 5px;
}
  