.channel-card {
  width: 356px;
  height: 326px;
  margin: auto;
  margin-top: -110px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  box-shadow: none;
}

.channel-card__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: #ffffff;
}

.channel-card__logo {
  width: 180px;
  height: 180px;
  margin-bottom: 16px;
  border: 1px solid #e3e3e3;
  border-radius: 50%;
}

.channel-card__icon {
  font-size: 14px !important;
  color: #808080;
  margin-left: 5px;
}

@media (min-width: 900px) {
  .channel-card {
    width: 320px;
  }
}
