.videos {
  flex-direction: row !important;
  flex-wrap: wrap;
  justify-content: start;
  gap: 16px;
}

.videos__item {
  width: 100%;
}

@media (min-width: 600px) {
  .videos__item {
    width: 320px;
  }
}

@media (min-width: 900px) {
  .videos__item {
    width: 358px;
  }

  .videos__column {
    flex-direction: column !important;
  }
}
