.search-bar {
  padding-left: 16px;
  border: 1px solid #e3e3e3;
  border-radius: 20px !important;
  box-shadow: none !important;
}

.search-bar__input {
  border: none;
  outline: none;
  width: 350px;
}

.search-bar__icon {
  padding: 10px !important;
  color: #ff0000 !important;
}

@media (min-width: 600px) {
  .search-bar {
    margin-right: 40px;
  }

  .search-bar__input {
    width: 200px;
  }
}
